import {AbstractDao, AbstractApi, Customer, InitializerProvider, IcConfig} from '..';
import {Model} from '../models/model';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';

// tslint:disable-next-line:no-shadowed-variable
export class AbstractRepository<T extends Model, R extends AbstractDao<T> | AbstractDao<T>, A extends AbstractApi<T> | AbstractApi<T>> {

  protected dao: AbstractDao<T>;
  protected api: AbstractApi<T>;
  protected icConfig: IcConfig;

  constructor(clazzT: new(obj: object) => T, clazzR: new(obj: object) => R, clazzA: new(...obj: any[]) => A, opt: {
    httpClient: HttpClient, initializerProvider: InitializerProvider
  }) {
    this.dao = new clazzR(clazzT);
    this.api = new clazzA(opt.httpClient, opt.initializerProvider);
    this.icConfig = opt.initializerProvider.CONFIG;
  }

  public async create(t: T): Promise<T> {
    // let customerID: CustomerID;
    try {
      if (['online', 'sync'].includes(this.icConfig.dataMode)) {
        t = await this.api.create(t);
      }
      if (['offline', 'sync'].includes(this.icConfig.dataMode)) {
        t = await this.dao.create(t);
      }
    } catch (err) {
      throw err;
    }
    return t;
  }


  public async update(t: T) {
    if (['online', 'sync'].includes(this.icConfig.dataMode)) {
      await this.api.update(t);
    }
    if (['offline', 'sync'].includes(this.icConfig.dataMode)) {
      await this.dao.update(t);
    }
  }

  public async replace(t: T) {
    if (['online', 'sync'].includes(this.icConfig.dataMode)) {
      await this.api.replace(t);
    }
    if (['offline', 'sync'].includes(this.icConfig.dataMode)) {
      await this.dao.replace(t);
    }
  }

  public async delete(id) {
    if (['online', 'sync'].includes(this.icConfig.dataMode)) {
      await this.api.delete(id);
    }
    if (['offline', 'sync'].includes(this.icConfig.dataMode)) {
      await this.dao.delete(id);
    }
  }

  public search(query: any): Observable<T[]> {
    // @ts-ignore
    const observable = new Observable<T[]>(async observer => {
      let result: T[];
      if (['offline', 'sync'].includes(this.icConfig.dataMode)) {
        result = await this.dao.search({
          fullName: {
            $regex: '.*' + query.textSearch + '.*'
          }
        });
        observer.next(result);
      }
      if (['online', 'sync'].includes(this.icConfig.dataMode)) {
        result = await this.api.search({...query});
        observer.next(result);
      }
      observer.complete();
    });
    return observable;
  }

  public get(id: string): Observable<T> {
    // @ts-ignore
    const observable = new Observable<T>(async observer => {
      let t: T;
      if (['offline', 'sync'].includes(this.icConfig.dataMode)) {
        t = await this.dao.get(id);
        observer.next(t);
      }
      if (['online', 'sync'].includes(this.icConfig.dataMode)) {
        t = await this.api.get(id);
        observer.next(t);
      }
      observer.complete();
    });
    return observable;
  }
}
